import React, { Component } from 'react'
import '../CssFolder/numberBtn.css'

class NumberBtn extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             value:1
        }
    }
    

    doDecrement=()=>{

        if (this.state.value >1) {

            this.setState({
                value:(this.state.value - 1)
            })            
        }


    }

    doIncrement=()=>{
        this.setState({
            value:(this.state.value + 1)
        })

    }

    render() {
        return (
            <div className="numbBtnDiv">
            <button onClick={this.doDecrement} className="minusBtn">-</button>
            <input type="text" className="number" value={this.state.value }></input>
            <button onClick={this.doIncrement} className="plusBtn">+</button>
            <sub>{this.state.message}</sub>
            </div>
        )
    }
}

export default NumberBtn
