import React, { Component } from 'react'

class TotalRs extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             rs:localStorage.getItem("total")
        }
    }
    
    render() {
        return (
            <div>
                {this.state.rs}
            </div>
        )
    }
}

export default TotalRs
