import React from 'react'
import { useStateValue } from './StateProvider';
import '../CssFolder/checkoutiteam.css'
import NumberBtn from './NumberBtn';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { Link } from '@material-ui/core';

function CheckOutItem({ id, title, price, imgUrl, weight }) {

    const [{ basket }, dispatch] = useStateValue();

    const removeFromBasket = () => {
        dispatch({
            type: "REMOVE_FROM_BASKET",
            id: id
        })
    }

    return (
        <div className="m_MainDiv">
            <img className="m_img" src={imgUrl} />

            <div className="m_info">

                <p className="m_title">{title}</p>
                <div className="m_priceDiv">
                    <small>Rs. </small>
                    <strong>{price}</strong>
                </div>

                <div className="m_priceDiv">
                    <small>for : </small>
                    <strong>{weight}</strong>
                </div>

                
                <div className="m_priceDiv">
                    <small>Total : </small>
                    <strong>{weight}</strong>
                </div>

<div className="nummmDiv">
                <NumberBtn/>

<DeleteOutlineIcon className="dleImg" onClick={removeFromBasket} />
</div>
            </div>
        </div>
    )
}

export default CheckOutItem
