import React from 'react'
import '../CssFolder/cart.css'
import { useStateValue } from './StateProvider';
import CheckOutItem from './CheckOutItem';
import TotalRs from './TotalRs.js'
import { getTotal } from './reducer';
import '../CssFolder/carttotal.css'

function Cart() {

    const [{ basket }, dispatch] = useStateValue();
    var total = 0;

    return (

        <div className="cart_MainDiv">
            {basket?.length === 0 ? (
                <div className="bsaketEmpty"><h5>Basket Empty</h5></div>
            ) : (
                    <div className="tp1">
                        <h5>Your Shopping basket</h5>

                        {basket.map(item => (
                            total = total + item.price,
                            localStorage.setItem("total", total),
                            <CheckOutItem
                                id={item.id}
                                title={item.title}
                                price={item.price}
                                imgUrl={item.imgUrl}
                                weight={item.weight} />
                        ))}
                    </div>
                )
            }

<div>
                <div className="t_MainDiv">

                    
                    <p >Total Rs. </p>
                    <p className="total_txt">{getTotal(basket)}</p>
                    <hr className="t_hr1"/>
                    <button className="t_proceedBtn">Procced</button>
                </div>
                <hr />
            </div>
        </div>
    )
}

export default Cart
